@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-inter: Inter;
  --font-roboto: Roboto;
  --font-montserrat: Montserrat;

  /* font sizes */
  --font-size-sm: 14px;
  --font-size-base: 16px;
  --font-size-11xl: 30px;
  --font-size-xs: 12px;
  --font-size-xl: 20px;

  /* Colors */
  --color-aliceblue-100: #f1f5f9;
  --color-aliceblue-200: #e2e8f0;
  --color-gray-100: #1e293b;
  --color-gray-200: #1f2937;
  --color-slategray: #64748b;
  --color-white: #fff;
  --color-mediumseagreen: #10b981;
  --color-lightslategray: #94a3b8;
  --color-mediumslateblue: #6366f1;
  --color-deepskyblue: #38bdf8;
  --color-darkslategray: #475569;
  --color-dimgray: #4b5563;

  /* border radiuses */
  --br-9xs: 4px;
  --br-2xs: 11px;
  --br-11xs: 2px;
  --br-lg: 18px;
}
